<!-- 买卡 -->
<template>
	<view class="">
		<!-- 遮罩层 -->
		<u-overlay :show="maskShow" @click="maskShow = false"></u-overlay>
		<!-- 列表 -->
		<view class="card">
			<uni-card v-for="(item, index) in newsList" :key="index"
				@click="goto(`/pages/orderDetail/orderDetail?_id=${item._id}`)">
				<!-- <text class="time">{{getYMDHMS(item.noticeSendTime)}}</text> -->
				<view class="">
					<!-- <text class="title">{{item._id}}</text> -->
					<view class="img-wrapper">
						<image class="pic" :src="item.coverImage.url"></image>
					</view>
					<view class="">
						<u-row justify="space-between" >
							<u-col span="10">
								<view class="demo-layout bg-purple">
									<text class="introduce">
										{{item.title}}
									</text>
								</view>
							</u-col>
						</u-row>
					</view>
					<u-row justify="space-between">
						<u-col span="10">
							<view class="demo-layout bg-purple">
								<text class="dateTime">
									{{getYMDHMS(item.dateTime)}}
								</text>
							</view>
						</u-col>
					</u-row>
				</view>
			</uni-card>
		</view>
		<view class="loading">{{loadingText}}</view>
	</view>
</template>
var _self, page = 1, timer = null;
<script>
	export default {
		name: "",
		data() {
			return {
				// 列表内容
				newsList: [{
					_id:"加载中",
					title: "加载中",
					dateTime: 0,
					isEnable: 1,
					coverImage: {
						"url": ""
					},
					imageList: [{
						"url": ""
					}, ]
				}],
				collectionName: "getOrder",
				loadingText: '加载中...', // 当前状态
				pageNum: 1, //默认页数
				pageSize: 4, //一页加载数据条数
				total: 0,
				// 遮罩
				maskShow: false,

			}
		},
		methods: {
			// 页面跳转
			goto(url) {
				uni.navigateTo({
					url: url
				})
			},
			// 获取列表数据
			getListData() {
				// 显示遮罩
				this.maskShow = true
				this.pageNum = 1;
				// 显示loading
				uni.showNavigationBarLoading();
				var that = this; // 重写this为that
				uniCloud.callFunction({
					name: that.collectionName,
					data: {
						"pageNum": that.pageNum,
						"pageSize": that.pageSize,
						"isEnable": 1
					},
					success: (res)=>  {
						console.log(" res:", res.result.data)
						// 赋值给列表
						that.newsList = res.result.data
						console.log(res.result.data);
						// 隐藏loading
						uni.hideNavigationBarLoading();
						// 停止下拉刷新加载
						uni.stopPullDownRefresh();
						// 关键，决定是否进行加载数据
						that.loadingText = '加载更多';
						console.log(res.result.data);
						// 隐藏遮罩
						that.maskShow = false
					},
				})
			},
			// 时间戳转年月日
			getYMDHMS(timestamp) {
				var date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
				var Y = date.getFullYear() + '-';
				var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
				var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
				var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
				var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
				var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());

				var strDate = Y + M + D + h + m + s;
				return strDate;
			},
			// 加载更多
			getMoreNews() {
				// 判断当前是否数据可加载TODO
				if (this.loadingText != '' && this.loadingText != '加载更多') {
					this.loadingText = "到底啦~"
					return false;
				}
				this.loadingText = '加载中...';
				//在当前页面显示导航条加载动画。
				uni.showNavigationBarLoading();
				// 页面加载
				this.pageNum++;
				// 调用云函数，获取列表
				uniCloud.callFunction({
						name: this.collectionName,
						data: {
							"pageNum": this.pageNum,
							"pageSize": this.pageSize,
							"isEnable": 1
						}
					})
					.then(res => {
						this.loadingText = '';
						console.log(res);
						// 新旧数据拼接
						this.newsList = this.newsList.concat(res.result.data);
						this.loadingText = '加载更多';
						// 判断是否加载完毕
						if (res.result.data.length == 0) {
							//在当前页面隐藏导航条加载动画。
							uni.hideNavigationBarLoading();
							this.loadingText = '已加载全部';
							return false;
						}
						// 隐藏loading
						uni.hideNavigationBarLoading();
					}) // success
					.catch((err) => {
						console.log(err);
					}) // fail
			},


		},
		created() {
			this.getListData()
		},
		onPullDownRefresh() {
			this.getListData()
		},
		//页面滚动到底部的事件
		onReachBottom() {
			this.getMoreNews();
		},
		onLoad() {
			this.getListData()
			// 当前页面
			console.log(`当前页面:order`)
		},
		// uniapp微信小程序分享页面到微信好友
		onShareAppMessage() {
			return {
				title: '智慧生活',
				path: '/pages/order/order' //分享的页面路径
			}
		},
		// uniapp微信小程序分享页面到微信朋友圈
		onShareTimeline() {
			return {
				title: '智慧生活 ',
				query: ``,
				imageUrl: 'https://vkceyugu.cdn.bspapp.com/VKCEYUGU-59f27697-00c5-4f1e-8c67-bb56e1e19e78/21819b35-232b-4636-8c1a-66783f601517.png',
			}
		},

	}
</script>

<style lang='scss'>
	page {
		background-color: #f7f7f7;
		padding-bottom: 30upx;
	}

	.card-item {
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	.dateTime {
		display: flex;
		align-items: right;
		justify-content: right;
		height: 80upx;
		padding-top: 10upx;
		font-size: 26upx;
		color: #fa3534;
	}

	.content {
		width: 710upx;
		padding: 0 24upx;
		background-color: #fff;
		border-radius: 4upx;
	}

	.title {
		display: flex;
		align-items: center;
		height: 90upx;
		font-size: 32upx;
		color: #303133;
	}

	.img-wrapper {
		width: 100%;
		height: 290upx;
		position: relative;
	}

	.pic {
		display: block;
		width: 100%;
		height: 100%;
		border-radius: 6upx;
	}

	.introduce {
		display: inline-block;
		font-size: 30upx;
		margin-left: 1%;
		margin-right: 1%;
		margin-top: 1%;
		margin-buttom: 1%;
		color: black;
		line-height: 38upx;
		color: #303133;
	}

	.bot {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 80upx;
		font-size: 24upx;
		color: #707070;
		position: relative;
	}

	.more-icon {
		font-size: 32upx;
	}

	.loading {
		text-align: center;
		color: #8F8F94;
	}

	.card {
		height: 30rpx;
		background: #ffffff;
		margin-top: 1%;
		margin-left: 1%;
		margin-right: 1%;
		border-radius: 15rpx;
		position: relative;
	}

	.originalPrice {
		text-decoration: line-through;
		color: #a4a7b0;
		font-size: 40rpx;
		margin-left: 25%;
		margin-right: 2%;

	}

	.presentPrice {
		color: orange;
		font-size: 60rpx;
		margin-left: 1%;
	}
</style>
